import { useStore } from '@/store'
import { onMounted,onBeforeUnmount } from 'vue'
import useCreateElement from '@/hooks/useCreateElement'


export default () => {
    const store = useStore();
    const { inertImageElementToSlider } = useCreateElement();
    //json
    const getSliders = ()=>{
        return store.state.slides[0];
    };



    const getApi = () => {
        return {
            inertImage:inertImageElementToSlider,
            getData:getSliders
        }
    }

    return {
        getApi
    }
}
