
    import {computed, defineComponent, onMounted, onBeforeUnmount} from 'vue'
    import {MutationTypes, ActionTypes, useStore} from '@/store'
    import api from '@/utils/api/api'

    import Editor from './views/Editor/index.vue'
    import Screen from './views/Screen/index.vue'

    export default defineComponent({
        name: 'app',
        components: {
            Editor,
            Screen,
        },
        setup() {
            const store = useStore()
            const screening = computed(() => store.state.screening)
            if (process.env.NODE_ENV === 'production') {
                window.onbeforeunload = () => false
            }
            const {getApi} = api();
            const boardApi = getApi();
            const receiveMessage = ((event: any) => {
                const evData = event.data;
                const data = evData.data;
                switch (evData.method) {
                    case 'getData':
                        sendMessage('getData',boardApi.getData());
                        break;
                    case 'inertImage':
                        boardApi.inertImage(data.src,data.width,data.height).then(()=>{
                            sendMessage('inertImage');
                        }).catch(e=>{
                            sendMessageFail('inertImage',e);
                        });
                        break;
                }
            })
            const sendMessage = (method:string,data?:object)=>{
                const dataObj = {method:method,result:{code:0,data:data,desc:""}};
                window.top.postMessage(JSON.stringify(dataObj),"*");
            }
            const sendMessageFail = (method:string,desc = "")=>{
                const dataObj = {method:method,result:{code:-1,data:null,desc:desc}};
                window.top.postMessage(JSON.stringify(dataObj),"*");
            }
            onMounted(() => {
                store.commit(MutationTypes.SET_AVAILABLE_FONTS)
                store.dispatch(ActionTypes.INIT_SNAPSHOT_DATABASE)
                window.addEventListener("message", receiveMessage, false);
                /*
                const {getApi} = api();
                const win: any = window;
                win.boardApi = getApi();*/
            })


            onBeforeUnmount(() => {
                window.removeEventListener("message", receiveMessage, false);
            });

            return {
                screening,
            }
        },
    })
